.popup {
  &-overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 3;
    background: #b3b3b3;
    display: flex;
    justify-content: center;
    align-items: center;
    &-box {
      background: #fff;
      padding: 36px 32px;
      position: relative;
      border-radius: 30px;
      width: 552px;
      display: flex;
      flex-direction: column;
      .btn--red {
        margin-top: auto; }
      &__close {
        position: absolute;
        top: 27px;
        right: 27px;
        &:hover {
          img {
            filter: brightness(.8); } } }
      &__title {
        font-size: 28px;
        margin-bottom: 16px; }
      &__descr {
        font-size: 14px;
        line-height: 24px;
        color: #808080;
        max-width: 424px;
        margin-bottom: 24px; }
      &__label {
        font-weight: 500; }
      &__input {
        margin-top: 12px;
        display: block;
        width: 100%;
        padding: 8px 10px;
        border-radius: 3px;
        border: 1px solid #DFE3E6;
        font-size: 14px;
        line-height: 24px;
        border-radius: 3px;
        &::-webkit-inner-spin-button {
          display: none;
          appearance: none; }
        &:hover {
          border: 1px solid #000; }
        &:focus {
          outline: none; } }
      &__error {
        font-size: 10px;
        color: #ea0029;
        margin-top: 4px; }
      &__calculate {
        color: #ea0029;
        margin-top: 12px;
        font-size: 14px;
        font-weight: 500;
        align-self: start;
        margin-bottom: 28px;
        &:hover {
          color: #f53a31; } }
      &__deductionTitle {
        font-size: 14px;
        margin-bottom: 5px; }
      &-list {
        margin-bottom: 20px;
        max-height: 40vh;
        overflow-y: auto;
        &::-webkit-scrollbar {
          appearance: none;
          width: 5px;
          border-radius: 6px;
          background: #0000000e; }
        &::-webkit-scrollbar-thumb {
          background: linear-gradient(180deg, #ff605b 0%, #e43938 100%);
          border-radius: 6px; }
        &__item {
          padding: 16px 0 16px 32px;
          border-bottom: 1px solid #DFE3E6;
          &__checkbox {
            display: none;
            &:checked + label {
              &::before {
                background: url('../../img/checked.svg') no-repeat center / cover;
                border: 1px solid transparent; } } }
          &__label {
            cursor: pointer;
            position: relative;
            span {
              color: #808080; }
            &::before {
              content: '';
              border: 1px solid #DFE3E6;
              width: 20px;
              height: 20px;
              background: #fff;
              position: absolute;
              left: -32px;
              top: -1px;
              border-radius: 6px; } } } }
      &-choice {
        margin-bottom: 40px;
        &__title {
          font-weight: 500; }
        &-buttons {
          display: inline-flex;
          margin-left: 32px;
          &__label {
            padding: 10px 13px;
            background: linear-gradient(90deg, #EEF0F2 0%, #EEF0F2 100%);
            font-weight: 400;
            font-size: 14px;
            border-radius: 50px;
            cursor: pointer;
            display: inline-block;
            &:not(:last-of-type) {
              margin-right: 16px; } }
          &__input {
            display: none;
            &:checked + label {
              color: #fff;
              background: linear-gradient(90deg, #ff605b 0%, #e43938 100%); } }
          &__title {
            font-size: 14px;
            font-weight: 500;
            display: inline-block;
            margin-bottom: 50px; } } } } } }

@media screen and (max-width: 570px) {
  .popup {
    &-overlay {
      &-box {
        width: 100%;
        height: 100vh;
        border-radius: 0;
        padding: 32px 16px;
        &-choice {
          &-buttons {
            margin-left: 0;
            margin-top: 24px;
            display: flex;
            &__label {
              &:not(:last-of-type) {
                margin-right: 8px; } } } } } } } }

@media screen and (max-width: 400px) {
  .popup {
    &-overlay {
      &-box {
        &__close {
          img {
            width: 12px;
            height: 12px; } }
        &__title {
          font-size: 18px; }
        &__descr {
          font-size: 12px;
          line-height: 16px; }
        &-list {
          &__item {
            font-size: 14px; } }
        &-choice {
          &__title {
            font-size: 14px; } } } } } }
